import {request} from '../../request.js'
export default {
	data() {
		return {
			flag: false,
			isMarginTop: false,
			navList:['店下','线上','销量','价格'],
			// 搜索结果模拟数据
			newsList: [],
			// 存储搜索的关键词
			searchText: '',
			$src: this.$src
		};
	},
	onShow() {
		this.upDateResult();
	},
	methods:{
		// 更新搜索结果
		upDateResult() {this.newsList = this.$store.state.searchResult;},
		//	监听搜索框输入的值
		handleSearch(event) {
			this.searchText = event.detail.value;
			
		},
		// 请求搜索数据
		searchData() {
			request({
				url: 'index.php?s=wap/goods/goodsSearchList',
				method: 'post',
				data: {
					sear_name: this.searchText
				}
			}).then(res => {
				if(res.data.code == 200) {
					// 提交到vuex做保存
					this.$store.commit('search',res.data);
					this.upDateResult();
				}
			})
		},
		// 点击搜索
		search() {
			if(this.searchText == '') {
				uni.showToast({
					title: '请输入要搜索的产品',
					icon: 'none'
				})
				return
			}
			else {
				// 储存搜索历史
				this.$store.commit('saveKeywords',{keywords: this.searchText});
				this.searchData();
				
			}
		
		},
		handleClick(item) {
			this.searchText = item;
			this.searchData();
		},	
		// 点击跳转到详情页
		handleGoodsDetail(id) {
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		}
	},
}
